:root {
    --background: 0 0% 100%;
    --foreground: 222.2 84% 4.9%;
    --card: 0 0% 100%;
    --card-foreground: 222.2 84% 4.9%;
    --popover: 0 0% 100%;
    --popover-foreground: 222.2 84% 4.9%;
    --primary: 187 100% 42%;
    --primary-foreground: 0 0% 100%;
    --secondary: 210 40% 96.1%;
    --secondary-foreground: 222.2 47.4% 11.2%;
    --muted: 210 40% 96.1%;
    --muted-foreground: 215.4 16.3% 46.9%;
    --accent: 210 40% 96.1%;
    --accent-foreground: 222.2 47.4% 11.2%;
    --destructive: 0 84.2% 60.2%;
    --destructive-foreground: 210 40% 98%;
    --border: 214.3 31.8% 91.4%;
    --input: 214.3 31.8% 91.4%;
    --ring: 187 100% 42%;
    --radius: 0.5rem;
    --navy: 210 100% 15%;
    --navy-foreground: 0 0% 100%;
  }

.dark {
    --background: 222.2 84% 4.9%;
    --foreground: 210 40% 98%;
    --card: 222.2 84% 4.9%;
    --card-foreground: 210 40% 98%;
    --popover: 222.2 84% 4.9%;
    --popover-foreground: 210 40% 98%;
    --primary: 187 100% 42%;
    --primary-foreground: 0 0% 100%;
    --secondary: 217.2 32.6% 17.5%;
    --secondary-foreground: 210 40% 98%;
    --muted: 217.2 32.6% 17.5%;
    --muted-foreground: 215 20.2% 65.1%;
    --accent: 217.2 32.6% 17.5%;
    --accent-foreground: 210 40% 98%;
    --destructive: 0 62.8% 30.6%;
    --destructive-foreground: 210 40% 98%;
    --border: 217.2 32.6% 17.5%;
    --input: 217.2 32.6% 17.5%;
    --ring: 187 100% 42%;
    --navy: 210 100% 15%;
    --navy-foreground: 0 0% 100%;
  }

.admin-theme {
    --background: 260 40% 8%;
    --foreground: 210 40% 98%;
    --card: 260 30% 12%;
    --card-foreground: 210 40% 98%;
    --popover: 260 35% 10%;
    --popover-foreground: 210 40% 98%;
    --primary: 187 100% 50%;
    --primary-foreground: 0 0% 100%;
    --secondary: 260 25% 18%;
    --secondary-foreground: 210 40% 98%;
    --muted: 260 25% 15%;
    --muted-foreground: 215 20.2% 65.1%;
    --accent: 187 100% 50%;
    --accent-foreground: 0 0% 100%;
    --destructive: 0 62.8% 50.6%;
    --destructive-foreground: 210 40% 98%;
    --border: 260 30% 20%;
    --input: 260 30% 18%;
    --ring: 187 100% 50%;
    --radius: 0.75rem;
  }

/* Apply glassmorphism to all cards and containers in admin theme */

.admin-theme .card,
  .admin-theme [class*="card"],
  .admin-theme div[class*="bg-white"],
  .admin-theme div[class*="bg-card"],
  .admin-theme section[class*="bg-"],
  .admin-theme .glass,
  .admin-theme .glass-card {
    background: rgba(15, 8, 30, 0.7) !important;
    backdrop-filter: blur(16px);
    -webkit-backdrop-filter: blur(16px);
    border: 1px solid rgba(0, 212, 232, 0.15) !important;
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.5);
  }

/* Apply glassmorphism to light theme cards and containers */

.light-theme .card,
  .light-theme [class*="card"],
  .light-theme div[class*="bg-white"],
  .light-theme div[class*="bg-card"],
  .light-theme section[class*="bg-"],
  .light-theme .glass,
  .light-theme .glass-card {
    background: rgba(255, 255, 255, 0.4) !important;
    backdrop-filter: blur(16px);
    -webkit-backdrop-filter: blur(16px);
    border: 1px solid rgba(255, 255, 255, 0.3) !important;
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.1);
  }

/* Make customer name and email text black in light mode */

.light-theme tbody a,
  .light-theme tbody td,
  .light-theme tbody div {
    color: #000000 !important;
  }

.light-theme tbody a:hover,
  .light-theme tbody a:hover div {
    color: #00BCD4 !important;
  }

/* Search bar and select styling in light mode */

.light-theme input {
    color: #000000 !important;
    border: 1px solid #d1d5db !important;
  }

.light-theme input::-moz-placeholder {
    color: #9ca3af !important;
  }

.light-theme input::placeholder {
    color: #9ca3af !important;
  }

.light-theme input:focus {
    border-color: #00BCD4 !important;
    outline: none !important;
    box-shadow: 0 0 0 2px rgba(0, 188, 212, 0.2) !important;
  }

/* Style select dropdowns in light mode */

.light-theme select {
    background-color: rgba(255, 255, 255, 0.9) !important;
    color: #374151 !important;
    border: 1px solid #d1d5db !important;
  }

.light-theme select:focus {
    border-color: #00BCD4 !important;
    outline: none !important;
    box-shadow: 0 0 0 2px rgba(0, 188, 212, 0.2) !important;
  }

.light-theme select option {
    background-color: #ffffff !important;
    color: #374151 !important;
  }

/* Make button text black in light mode (except gradient buttons) */

.light-theme button:not([style*="gradient"]) {
    color: #000000 !important;
  }

/* Make all card content text black in light mode */

.light-theme h1,
  .light-theme h2,
  .light-theme h3,
  .light-theme h4,
  .light-theme h5,
  .light-theme h6,
  .light-theme p,
  .light-theme span,
  .light-theme label {
    color: #000000 !important;
  }

/* Apply glass effect to buttons and inputs */

.admin-theme button:not([class*="gradient"]):not([style*="background"]),
  .admin-theme input,
  .admin-theme textarea,
  .admin-theme select {
    background: rgba(10, 5, 20, 0.5);
    border-color: rgba(0, 212, 232, 0.2);
  }

.admin-theme input:focus,
  .admin-theme textarea:focus,
  .admin-theme select:focus {
    border-color: rgba(0, 212, 232, 0.5);
    box-shadow: 0 0 0 2px rgba(0, 212, 232, 0.1);
  }

