/* Custom scrollbar for admin theme */
.admin-theme * {
  scrollbar-width: thin;
  scrollbar-color: #00D4E8 rgba(30, 15, 50, 0.5);
}

.admin-theme *::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.admin-theme *::-webkit-scrollbar-track {
  background: rgba(30, 15, 50, 0.5);
  border-radius: 3px;
}

.admin-theme *::-webkit-scrollbar-thumb {
  background: linear-gradient(135deg, #00D4E8 0%, #00BCD4 100%);
  border-radius: 3px;
}

.admin-theme *::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(135deg, #00BCD4 0%, #0097A7 100%);
}

.admin-theme *::-webkit-scrollbar-button {
  display: none;
}

.admin-theme *::-webkit-scrollbar-corner {
  background-color: transparent;
}

/* Default scrollbar */
* {
  scrollbar-width: thin;
  scrollbar-color: #00D4E8 #f1f1f1;
  scrollbar-arrow-color: transparent;
}

*::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

*::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

*::-webkit-scrollbar-thumb {
  background-color: #00D4E8;
  border-radius: 3px;
}

*::-webkit-scrollbar-thumb:hover {
  background-color: #00bcd4;
}

*::-webkit-scrollbar-button {
  display: none;
  width: 0;
  height: 0;
}

*::-webkit-scrollbar-corner {
  background-color: transparent;
}


/* Force calendar grid layout - react-day-picker v9 */
.rdp {
  --rdp-accent-color: #00D4E8;
  --rdp-background-color: #00D4E8;
}

.rdp-month {
  width: 100%;
}

.rdp-table {
  width: 100%;
  max-width: 100%;
}

.rdp-head,
.rdp-tbody {
  width: 100%;
}

.rdp-head_row,
.rdp-week {
  display: grid !important;
  grid-template-columns: repeat(7, minmax(0, 1fr)) !important;
  width: 100% !important;
  gap: 0.5rem;
}

.rdp-head_cell,
.rdp-day {
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  width: 100% !important;
  aspect-ratio: 1;
}

.rdp-day_button {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Fix selected date styling */
.rdp-day_button[aria-selected="true"],
.rdp-day_button.rdp-selected {
  background-color: #00D4E8 !important;
  color: white !important;
  border-radius: 0.5rem;
}

.rdp-day_button[aria-selected="true"]:hover,
.rdp-day_button.rdp-selected:hover {
  background-color: #00BCD4 !important;
  color: white !important;
}

/* Custom animations */
@keyframes slide-up {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scale-in {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.animate-slide-up {
  animation: slide-up 0.6s ease-out;
}

.animate-scale-in {
  animation: scale-in 0.5s ease-out;
}

.animate-fade-in {
  animation: fade-in 0.4s ease-out;
}

/* Gradient text */
.gradient-text {
  background: linear-gradient(135deg, #00BCD4 0%, #00D4E8 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.text-gradient {
  background: linear-gradient(135deg, #00BCD4 0%, #00D4E8 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.gradient-primary {
  background: linear-gradient(90deg, #00BCD4 0%, #00D4E8 100%);
}

/* Card shadows */
.card-shadow {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

/* Glow effect */
.glow-effect {
  box-shadow: 0 0 20px rgba(0, 188, 212, 0.3);
}

/* Gradient card */
.gradient-card {
  background: linear-gradient(135deg, rgba(0, 188, 212, 0.02) 0%, rgba(0, 212, 232, 0.02) 100%);
}

/* Navy background */
.bg-navy {
  background-color: hsl(var(--navy));
}

.text-navy-foreground {
  color: hsl(var(--navy-foreground));
}

/* Glassmorphism utilities - Dark Mode */
.glass {
  background: rgba(10, 5, 20, 0.6);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(0, 212, 232, 0.15);
}

.glass-card {
  background: rgba(15, 8, 30, 0.7);
  backdrop-filter: blur(16px);
  -webkit-backdrop-filter: blur(16px);
  border: 1px solid rgba(0, 212, 232, 0.15);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.5);
}

.glass-sidebar {
  background: rgba(10, 5, 20, 0.85);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-right: 1px solid rgba(0, 212, 232, 0.2);
}

.glass-header {
  background: rgba(15, 8, 30, 0.8);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  border-bottom: 1px solid rgba(0, 212, 232, 0.15);
}

/* Glassmorphism utilities - Light Mode */
.light-theme .glass {
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
