}

/* Animated background - Dark Mode */
.void-bg {
  background-color: #0a0015;
  background-image: 
    linear-gradient(rgba(10, 0, 21, 0.7), rgba(10, 0, 21, 0.7)),
    url('/images/admin-bg.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  position: relative;
  overflow-x: hidden;
}

/* Animated background - Light Mode */
.light-theme {
  background-color: #f0f9ff;
  background-image: 
    linear-gradient(rgba(240, 249, 255, 0.3), rgba(240, 249, 255, 0.3)),
    url('/images/light-bg.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  position: relative;
  overflow-x: hidden;
}

.void-bg::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 50%, rgba(0, 212, 232, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(138, 43, 226, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 50% 20%, rgba(0, 188, 212, 0.05) 0%, transparent 40%);
  pointer-events: none;
  z-index: 0;
  animation: pulse-glow 8s ease-in-out infinite;
}

.void-bg::after {
  content: '';
  position: fixed;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: 
    repeating-linear-gradient(
      0deg,
      transparent,
      transparent 2px,
      rgba(0, 212, 232, 0.03) 2px,
      rgba(0, 212, 232, 0.03) 4px
    );
  pointer-events: none;
  z-index: 0;
  animation: grid-move 20s linear infinite;
}

@keyframes pulse-glow {
  0%, 100% {
    opacity: 0.6;
  }
  50% {
    opacity: 1;
  }
}

@keyframes grid-move {
  0% {
    transform: translate(0, 0);
  }
  100% {
